



.hero-main{
    background-image: url('/assets/img/bg-image.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    position: relative;
    &::after{
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        opacity: 50%;
        background: radial-gradient(100% 100% at 13.65% 88.98%, #FFBA53 0%, rgba(255, 255, 255, 0.0001) 100%), 
        radial-gradient(100% 100% at 25.26% 21.66%, #CBEEF0 0%, rgba(255, 255, 255, 0.0001) 100%), #FFFFFF;
        z-index: 1;
    }
    &::before{
        content: "";
        position: absolute;
        width: 50%;
        height: 24px;
        background-color: $color1;
        border-radius: 16px;
        bottom: -12px;
        left: 50%;
        transform: translateX(-50%);
        z-index: -1;
    }
    .hero-main-inr{
        position: relative;
        z-index: 10;
        .container{
            .navbar-otr{
                .navbar-inr{
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    padding: 56px 0 0 0;
                    position: relative;
                    .logo-otr{
                        .logo{
                            width: 120px;
                        }
                    }
                    .navigation-ul{
                        display: flex;
                        align-items: center;
                        @include breakpoint($secreen-max-sm){
                            position: absolute;
                            width: 100%;
                            flex-direction: column;
                            align-items: baseline;
                            bottom: -280px;
                            background: $color2;
                            border-radius: 16px;
                            padding: 24px;
                            opacity: 0;
                            visibility: hidden;
                            transition: .3s;
                        }
                        .navigation-li{
                            &:not(:last-child){
                                margin-right: 50px;
                                @include breakpoint($secreen-max-sm){
                                    margin-bottom: 20px;
                                    margin-right: 0;
                                }
                            }
                            .navigation-a{
                                font-size: 18px;
                                font-family: $font-family-Montserrat-Regular;
                                color: $color2;
                                text-decoration: underline;
                                transition: .2s;
                                @include breakpoint($secreen-max-sm){
                                    color: $color-white;
                                }
                                &:hover{
                                    color: $color1;
                                }
                            }
                        }
                        .btn-li{
                            margin: 30px 0 24px 0;
                            @include breakpoint($secreen-min-sm-767){
                                display: none;
                            }
                            .btn-custom{
                                background-color: $color1;
                            }
                        }
                    }
                    .height-full{
                        opacity: 1;
                        visibility: visible;
                    }
                    .action{
                        width: auto;
                        @include breakpoint($secreen-max-sm){
                            display: none;
                        }
                    }
                    .menu-icon-otr{
                        cursor: pointer;
                        @include breakpoint($secreen-min-sm-767){
                            display: none;
                        }
                        .menu-icon-inr{
                            fill: $color1;
                        }
                    }
                }
            }
            .hero-inr{
                .hero-row{
                    padding: 106px 0 50px 0;
                    .col-content-otr{
                        .col-content-inr{
                            .heading{
                                font-size: 48px;
                                line-height: 56px;
                                font-family: $font-family-Montserrat-Regular;
                                margin-bottom: 21px;
                                .bold-text{
                                    font-family: $font-family-Montserrat-Bold;
                                }
                            }
                            .desc{
                                font-size: 21px;
                                line-height: 34px;
                                font-family: $font-family-Montserrat-Bold;
                            }
                            .para{
                                font-family: $font-family-Montserrat-SemiBold;
                                margin-bottom: 24px;
                            }
                            .form-main{
                                margin: 28px 0 88px 0;
                                .input-row{
                                    display: flex;
                                    margin-bottom: 25px;
                                    @include breakpoint($secreen-max-sm){
                                        flex-direction: column;
                                    }
                                    .input-last-name{
                                        margin-left: 25px;
                                        @include breakpoint($secreen-max-sm){
                                            margin: 25px 0 0 0;
                                        }
                                    }
                                }
                                .input-otr{
                                    width: 100%;
                                    background-color: $color-white;
                                    border-radius: 16px;
                                    border: 2px solid $color2;
                                    display: flex;
                                    align-items: center;
                                    .input-email{
                                        width: 100%;
                                        border: none !important;
                                    }
                                    .submit-btn{
                                        margin-right: 10px;
                                        &:focus{
                                            border: none;
                                        }
                                    }
                                }
                            }
                        }
                    }
                    .col-img-otr{
                        .col-img-inr{
                            display: flex;
                            align-items: flex-end;
                            justify-content: flex-end;
                            @include breakpoint($secreen-max-md){
                                align-items: center;
                                justify-content: center;
                            }
                            .img-hero{
                                width: 500px;
                            }
                        }
                    }
                    .scroll-action{
                        @include breakpoint($secreen-max-md){
                            margin: 50px 0 0 0;
                            display: flex;
                            justify-content: center;
                        }
                        .learn-more{
                            width: 260px;
                        }
                    }
                }
            }
        }
    }
}

.who-gritly{
    padding: 100px 0 40px 0;
    background-image: url('/assets/img/bg-image.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    .container{
        .main-heading{
            .heading{
                font-size: 48px;
                line-height: 56px;
                font-family: $font-family-Montserrat-ExtraBold;
                padding: 0 0 10px 0;
                .gritly-text{
                    font-family: $font-family-Montserrat-ExtraBold;
                    font-size: 100px;
                    line-height: 130px;
                }
            }
            .desc{
                font-size: 21px;
                line-height: 34px;
                font-family: $font-family-Montserrat-SemiBold;
            }
            .para{
                margin: 10px 0 30px 0;
            }
        }
        .row-custom{
            .col-otr{
                .col-inr{
                    background: radial-gradient(273.1% 273.1% at 42% 37.29%, #FFFFFF 17.71%, #FF8D02 100%);
                    box-shadow: 0px 24px 48px rgba(50, 54, 72, 0.08);
                    border-radius: 24px;
                    padding: 30px 25px 20px 25px;
                    .icon-otr{
                        width: 95px;
                        margin: 0 auto;
                        .icon{
                            width: 95px;
                        }
                    }
                    .desc-inr{
                        margin: 30px 0 0 0;
                        color: $color2;
                        font-size: 21px;
                        line-height: 34px;
                        font-family: $font-family-Montserrat-SemiBold;
                        text-align: center;
                    }
                }
                .box1,.box2{
                    @include breakpoint($secreen-max-md){
                        margin-bottom: 24px;
                    }
                }
            }
        }
        .col-img-otr{
            margin: 0 auto;
            width: 750px;
            margin: 73px auto 0 auto;
            @include breakpoint($secreen-max-md){
                width: 100%;
            }
            .shadow-img{
                width: 750px;
                @include breakpoint($secreen-max-md){
                    width: 100%;
                }
            }
            .need-more-info{
                color: $color2;
                font-size: 48px;
                line-height: 56px;
                font-family: $font-family-Montserrat-SemiBold;
                text-align: center;
            }
        }
    }
}

.join-main{
