                                }
                            }
                        }
                    }
                }
                .shadow-img-otr{
                    width: 750px;
                    margin: 0 auto;
                    @include breakpoint($secreen-max-sm){
                        width: 100%;
                    }
                    .shadow-img{
                        width: 750px;
                        margin: 0 auto;
                        text-align: center;
                    }
                }
            }
        }
    }
}

.discover-main{
    padding: 150px 15px 53px 15px;
    background-color: $color2;
    overflow: hidden;
    .container{
        .row-custom{
            .col-content-otr{
                .col-content-inr{
                    padding-right: 120px;
                    @include breakpoint($secreen-max-md){
                        padding: 0;
                    }
                    .main-heading{
                        font-size: 45px;
                        line-height: 52px;
                        color: $color-white;
                        font-family: $font-family-Montserrat-ExtraBold;
                    }
                    .heading{
                        margin: 47px 0 17px 0;
                        font-size: 59px;
                        line-height: 93px;
                        color: $color-white;
                        font-family: $font-family-Montserrat-Regular;
                    }
                    .form-main{
                        .input-row{
                            display: flex;
                            margin-bottom: 25px;
                            @include breakpoint($secreen-max-sm){
                                flex-direction: column;
                            }
                            .input-last-name{
                                margin-left: 25px;
                                @include breakpoint($secreen-max-sm){
                                    margin: 25px 0 0 0;
                                }
                            }
                        }
                        .input-otr{
                            .input-email{
                                width: 100%;
                            }
                        }
                        .action{
                            margin: 50px 0 0 0;
                            .submit-btn{
                                background-color: $color1;
                                color: $color-white;
                                border: none;
                                text-decoration: underline;
                                transition: .2s;
                                &:focus{
                                    border: none;
                                }
                                &:hover{
                                    color: $color2;
                                }
                            }
                        }
                    }
                }
            }
            .col-text-otr{
                .col-text-inr{
                    @include breakpoint($secreen-max-md){
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        align-items: center;
                        margin: 100px 0;
                    }
                    .box-main{
                        padding: 72px 40px;
                        border-radius: 16px;
                        position: relative;
                        z-index: 10;
                        width: 400px;
                        @include breakpoint($secreen-xs){
                            // background-color: $color-white;
                            // border-radius: 24px;
                            width: 100%;
                        }
                        &::before{
                            content: "";
                            position: absolute;
                            left: 0;
                            top: 0;
                            width: 100%;
                            height: 100%;
                            background-color: $color-white;
                            border-radius: 72px;
                            z-index: -1;
                            transform: skewX(-4deg);
                            
                        }   
                        &::after{
                            content: "";
                            position: absolute;
                            left: -10px;
                            top: 0px;
                            width: calc(100% + 0px);
                            height: calc(100% + 0px);
                            background-color: $color1;
                            border-radius: 72px;
                            z-index: -2;
                            transform: skewX(-9deg) rotate(-20deg);
                            @include breakpoint($secreen-max-sm){
                                transform: skewX(-9deg )rotate(-10deg);
                            }
                            
                        }   
                    }
                    .text-div{
                        &:not(:last-child){
                            margin-bottom: 30px;
                        }
                        .text-head{
                            color: $color1;
                            font-size: 25px;
                            font-family: $font-family-Montserrat-ExtraBold;
                        }
                        .desc-desc{
                            color: $color2;
                            font-family: $font-family-Montserrat-SemiBold;
                            font-size: 15px;
                            margin-top: 20px;
                        }
                    }
                    .links-ul{
                        display: flex;
                        align-items: flex-end;
                        @include breakpoint($secreen-max-sm){
                            flex-wrap: wrap;
                            margin: 100px 0 0 0;
                        }
                        .links-li{
                            &:not(:last-child){
                                margin-right: 50px;
                            }
                            .links-a{
                                font-size: 18px;
                                font-family: $font-family-Montserrat-Regular;
                                color: #FAFBFF;
                                text-decoration: underline;
                                transition: .2s;
                                &:hover{
                                    color: $color1;
                                }
                                .logo-img{
                                    width: 73px;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

