.clients-main{
    background-image: url('/assets/img/bg-image.png');
    background-repeat: no-repeat;
    background-position: center center;
    background-size: cover;
    .container{
        .heading{
            color: $color1;
            text-align: center;
            font-family: $font-family-Montserrat-SemiBold;
            font-size: 46px;
            margin: 0 0 100px 0;
        }
        .logos-otr{
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
            justify-content: center;
            align-items: center;
            grid-gap: 70px;
            .logos-inr{
                @include breakpoint($secreen-max-md){
                    display: flex;
                    align-items: center;
                    justify-content: center;
                }
                .img-partner{
                    width: auto;
                }
            }
        }
        .shadow-img-otr{
            width: 750px;
            margin: 60px auto 0 auto;
            @include breakpoint($secreen-max-sm){
                width: 100%;
            }
            .shadow-img{
                width: 750px;
                @include breakpoint($secreen-max-sm){
                    width: 100%;
                }
            }
        }
    }
}

