
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    &:hover {
        text-decoration: none;
    }
}

p,h1,h2,h3,h4,h5,h6,ul,li{
    padding: 0;
    margin: 0;
}

.btn-custom{
    border-radius: 16px;
    color: $color-white;
    padding: 23px 22px 20px 22px;
    background-color: $color2;
    font-family: $font-family-Montserrat-Medium;
    font-size: 18px;
    transition: .2s;
    &:hover{
        text-decoration: underline;
        color: $color-white;
        background-color: $color1;
    }
}

.btn-custom2{
    padding: 16px 24px;
    background-color: $color1;
    color: $color-white;
    font-family: $font-family-Montserrat-Bold;
    border-radius: 16px;
    border: none;
    text-decoration: underline;
    transition: .2s;
    &:hover{
        text-decoration: underline;
        color: $color-white;
        background-color: $color2;
    }
}

.input{
    padding: 26px 30px;
    border: 2px solid $color2;
    border-radius: 16px;
    font-size: 16px;
    width: 100%;
    font-family: $font-family-Montserrat-Regular;
    color: $color-3;
    &::placeholder{
        color: $color-3;  
    }
    &:focus{
        outline: none;
    }
}

